// 
// Color Variables
// 

$white: #fff;
$black: #000;
$clear: transparent;

$primary: hsl(238, 91%, 65%);
$secondary: #00CCBE;
$success: #017b00;
$info: #007db1;
$warning: #cc0000;
$danger: #ee453d;

// Text Coloring
$text-strong:           #1d1d23;
$text-medium:           #717171;
$text-weak:             rgba(113, 113, 113, 0.3);
$text-white:            $white;
$text-neutral:          #f0f2f3;
$text-neutral-light:    #f8f9fa;
$text-neutral-dark:     #e1e3e4;

$text-primary:          $primary;
$text-secondary:        $secondary;
$text-success:          $success;
$text-info:             $info;
$text-warning:          $warning;
$text-danger:           $danger;

$color-text: (
  strong:               #1d1d23,
  medium:               #717171,
  weak:                 rgba(113, 113, 113, 0.3),
  white:                $white,
  neutral:              #f0f2f3,
  neutral-light:        #f8f9fa,
  neutral-dark:         #e1e3e4,
  primary:              $primary,
  secondary:            $secondary,
  success:              $success,
  info:                 $info,
  warning:              $warning,
  danger:               $danger
);



// Fill Coloring
$fill-neutral:          #f0f2f3;
$fill-neutral-light:    #f8f9fa;
$fill-neutral-dark:     #e1e3e4;
$fill-white:            $white;
$fill-black:            #1D1D23;

$fill-primary:          $primary;
$fill-primary-light:    tint($primary, 25);
$fill-primary-dark:     shade($primary, 25);

$fill-secondary:        $secondary;
$fill-secondary-light:  tint($secondary, 25);
$fill-secondary-dark:   shade($secondary, 25);

$fill-success-strong:   $success;
$fill-success-weak:     tint($success, 95);
$fill-info-strong:      $info;
$fill-info-weak:        tint($info, 95);
$fill-danger-strong:    $danger;
$fill-danger-weak:      tint($danger, 95);
$fill-warning-strong:   $warning;
$fill-warning-weak:     tint($warning, 95);


$color-fill: (
  neutral:              #f0f2f3,
  neutral-light:        #f8f9fa,
  neutral-dark:         #e1e3e4,
  white:                $white,
  black:                #1D1D23,
  primary:              $primary,
  primary-light:        tint($primary, 25),
  primary-dark:         shade($primary, 25),
  secondary:            $secondary,
  secondary-light:      tint($secondary, 25),
  secondary-dark:       shade($secondary, 25),
  success-strong:       $success,
  success-weak:         tint($success, 95),
  info-strong:          $info,
  info-weak:            tint($info, 95),
  danger-strong:        $danger,
  danger-weak:          tint($danger, 95),
  warning-strong:       $warning,
  warning-weak:         tint($warning, 95)
);      
      

// Border Coloring
$border-neutral:          #e0e0e0;
$border-neutral-light:    #f8f9fa;
$border-neutral-dark:     #bdbdbd;
$border-white:            $white;
$border-primary:          $primary;
$border-secondary:        $secondary;
$border-success:          $success;
$border-info:             $info;
$border-warning:          $warning;
$border-danger:           $danger;

$color-border: (
  neutral:                #e0e0e0,
  neutral-light:          #f8f9fa,
  neutral-dark:           #bdbdbd,
  white:                  $white,
  primary:                $primary,
  secondary:              $secondary,
  success:                $success,
  info:                   $info,
  warning:                $warning,
  danger:                 $danger
);      


// $border-radius-lg: 16px;
// $border-radius-sm: 4px;

// Shadows: box-shadow property
$shadow: (
  small: 0px 2px 0px rgba(0, 0, 0, 0.1),
  medium: 0px 5px 10px rgba(0, 0, 0, 0.12),
  large: 0px 20px 30px -10px rgba(0, 0, 0, 0.12)
);

// Bootstrap shadows
$box-shadow: map-get($shadow,'medium');
$box-shadow-sm: map-get($shadow,'small');
$box-shadow-lg: map-get($shadow,'large');

// 
// Type Variables
// 


$font-family-sans-serif: Rubik, system-ui, -apple-system, "Segoe UI", Roboto, "Helvetica Neue", Arial, "Liberation Sans", sans-serif, "Apple Color Emoji", "Segoe UI Emoji", "Segoe UI Symbol", "Noto Color Emoji" !default;
$headings-font-family: Rubik, system-ui, -apple-system, "Segoe UI", Roboto, "Helvetica Neue", Arial, "Liberation Sans", sans-serif, "Apple Color Emoji", "Segoe UI Emoji", "Segoe UI Symbol", "Noto Color Emoji" !default;
$line-height: 1.5;

$desktop-type-map: (
  xl: 1.5, 
  lg: 1.25, 
  md: 1.125, 
  sm: 1, 
  xs: .9375
);

$mobile-type-map: (
  xl: 2, 
  lg: 1.8, 
  md: 1.6, 
  sm: 1.5,
  xs: 1.4
);

$display-font-sizes: (
  1: 5.375rem,
  2: 4rem,
  3: 3.375rem,
  4: 2.75rem,
  5: 2.375rem,
  6: 2rem
) !default;

$font-weights: (
  weak: 400,
  medium: 500,
  strong: 700
);

// $link-color: $fill-primary-dark;

$grid-gutter-width: 32px;
$el-pad: $grid-gutter-width / 2;
$table-sides: $el-pad / 2;

// scss-docs-start navbar-theme-variables
