h1, .h1, *[class~='heading-xl'] {
  @extend %heading-xl-medium;
}

h2, .h2, *[class~='heading-lg'] {
  @extend %heading-lg-medium;
}

h3, .h3, *[class~='heading-md'] {
  @extend %heading-md-medium;
}

h4, .h4, *[class~='heading-sm'] {
  @extend %heading-sm-medium;
}

h5, .h5, *[class~='heading-xs'] {
  @extend %heading-xs-medium;
}

a {
  color: var(--text-link-color);
  font-weight: 500;

  &:hover {
    color:  var(--text-link-hover-color);
  }
}

@each $size-name, $scale-step in $desktop-type-map {
  .body-#{$size-name}-weak {
    @extend %body-#{$size-name}-weak;
  }

  .body-#{$size-name}-medium {
    @extend %body-#{$size-name}-medium;
  }

  .heading-#{$size-name}-weak {
    @extend %heading-#{$size-name}-weak;
  }

  .heading-#{$size-name}-medium {
    @extend %heading-#{$size-name}-medium;
  }

  .heading-#{$size-name}-strong {
    @extend %heading-#{$size-name}-strong;
  }
}

@include responsive(xxs) {
  html {
    font-size: 10px;
  }
}


*[class*="display"] {
  // font-family: $headings-font-family;
  font-weight: 500;
  line-height: 1.3 !important;
}

@include responsive(sm-up) {
  .display {
    &-xl {
      font-size: map-get($display-font-sizes, 1);
    }

    &-lg {
      font-size: map-get($display-font-sizes, 2);
    }

    &-md {
      font-size: map-get($display-font-sizes, 3);
    }

    &-sm {
      font-size: map-get($display-font-sizes, 4);
    }

    &-xs {
      font-size: map-get($display-font-sizes, 5);
    }

    &-xxs {
      font-size: map-get($display-font-sizes, 6);
    }
  }
}


@include responsive(xs) {
  *[class^='display-'] {
    &[class$='xl'] {
      font-size: 3.8rem;
    }

    &[class$='lg'] {
      font-size: 3.4rem;
    }

    &[class$='md'] {
      font-size: 3.0rem;
    }

    &[class$='sm'] {
      font-size: 2.6rem;
    }

    &[class$='xs'] {
      font-size: 2.4rem;
    }

    &[class$='xxs'] {
      font-size: 2.2rem;
    }
  }
}

*[data-icon]::before {
  content: "";
  width: .75em;
  height: .75em;
  margin-right: .35em;
  display: inline-block;
}
*[data-icon=substack]::before {
  content: "";
  background-repeat: no-repeat;
  background-image: url('/images/ui/icon_lg_substack.svg');
  background-size: 80%;
  background-position: left bottom;
  margin-right: 0.15em;
}

