@import "../config/mixins.scss";
@import "../config/design-token";
@import "../config/typography";
@import "../config/color-system";
@import "../config/lib-placeholder-selectors";

$dash-break: 1290px;
$clip-open: polygon(0% 0%, 100% 0, 100% 100%, 0 100%);
$clip-close: polygon(0% 0%, 0% 0, 0% 100%, 0 100%);

%retractable {
  clip-path: $clip-close;
  transition: clip-path .5s ease, width 1s linear 10s,  max-width .5s ease, width 1s linear 10s;
  overflow-x: hidden;
  width: auto;
  max-width: 0px;
  display: none;

  @include responsive(lg) {
    clip-path: $clip-open;
    transition: clip-path .5s ease, width .5s linear 0s,  max-width .5s ease, width .5s linear 0s;
    max-width: 500px;
    display: block;
  }
}

.navbar-dash {
  background-color: white;
  border-bottom: 1px solid $border-neutral;
  padding: beat(1) $el-pad;
  position: relative;
}

.navbar-nav {
  align-items: center;
  display: flex;
  flex-direction: row;
  gap: 0 1rem;
}

.notifications {
  max-width: 480px;
  position: absolute;
  right: 3rem;
  top: 3.75rem;
  width: 90%;
  z-index: 2000;

  .card {
    @extend %shadow-large;

    padding: 1rem;
  }

  .notification {
    display: flex;
    gap: $grid-gutter-width;
    margin-top: beat(8);
  }

  a, img {
    height: 100%;
    width: 100%;
  }

  figure {
    @extend %shadow-medium;

    background-color: pink;
    border: 2px solid white;
    border-radius: 50%;
    flex: 0 0 44px;
    height: 44px;
    overflow: hidden;
  }
}

.inner-content {
  padding: beat(4) $grid-gutter-width beat(8);
}

.profile-grid {
  display: grid;
  max-width: 1000px;
  margin: beat(6) auto;
  grid-template-columns: minmax(60%, 588px) minmax(40%, 282px);
  grid-template-rows: repeat(auto-fill, auto);
  gap: beat(4) $grid-gutter-width;
  padding: 0 $el-pad;

  & > * {
    grid-column: 1/2;
  }

  .side-content {
    grid-column: 2 / 3;
    grid-row: 1/-1;

    .weblink {
      display: flex;
      align-items: center;

      &::before {
        background-image: url('../../images/ui/socialmedia/icon-webglobe.svg');
        content: '';
        display: inline-block;
        height: 1em;
        width: 1em;
        margin-right: .35em;
      }
    }
    .badge {
      background-color: map-get($color-fill, 'neutral');
      color: map-get($color-text, 'medium');
    }

  }

  .reasons {
    .btn {
      align-self: flex-start;
    }    
  }


  a:before {
    // background: url('../images/ui/socialmedia/icon-facebook.svg') center no-repeat;
    // background-size: 100%;
    // content: '';
    height: 1.5rem;
    width: 1.5rem;
    // display: block;
    // border-radius: 50%;

  }
  a[href~='twitter.com']:before {
    background-image: url('../../images/ui/socialmedia/icon-twitter-dark.svg');
  }
  a[href~='instagram.com']:before {
    background-image: url('../../images/ui/socialmedia/icon-instagram-dark.svg');
  }
  a[href~='youtube.com']:before {
    background-image: url('../../images/ui/socialmedia/icon-youtube-dark.svg');
  }


}

.profile-head {
  .spliter {
    justify-content: space-between;
    align-items: flex-end;

    & > * {
      flex: 0 1 auto;
    }

    p {
      margin: 0;  
    }
  }

  .profile-photo {
    height: 128px;
    width: 128px;
    overflow: hidden;
    border-radius: 50%;
    border: 3px solid $border-white;
    box-shadow: $box-shadow-sm;

  }


}


@include responsive(xs){
  .profile-grid {
    display: block;

    .card + .card {
      margin-top: beat(4);
    }

    .side-content {
      margin: beat(4) 0;
    }
  }

};

