.touch-toggle {
  display: none;

  button {
    height: 65px;
    padding: beat(3) $grid-gutter-width;
  }
}

.user-display {
  @extend %retractable;

  padding: beat(3) 2rem;

  dl {
    margin-bottom: 0;

    dt {
      @extend %heading-md-medium;
    }
    dd {
      @extend %heading-xs-weak;
    }
  }

}

.controls {
  background-color: white;
  display: flex;
  flex-direction: column;

  // max-width: 5rem;
  min-width: 100%;
  transition: max-width .5s ease 0s;

  nav {
    flex: 1;
  }

  ul {
    display: flex;
    height: 100%;
    flex-direction: column;
    margin: 0;
    padding: 0;

    & > *:last-child {
      margin-top: auto;
    }
  }

  nav li > * {
    @extend %heading-sm-weak;

    display: flex;
    align-items: center;
    padding: beat(3) 2rem;
    text-decoration: none;
    padding: beat(3) $grid-gutter-width;

    &.active {
      background-color: $fill-neutral;
    }
  }

  a > span {
    width: auto;
    padding-left: $el-pad;
    white-space: nowrap;
  }

  &:hover a span {
    max-width: 500px;
    display: block;
  }

  svg {
    height: 24px;
    min-width: 30px;
    max-width: 30px;
  }
}

// // Content

.inner-content {
  display: flex;
  flex-wrap: wrap;
  gap: 0 $grid-gutter-width;
  justify-content: center;
  margin: 0 auto;
  max-width: 1400px;
  padding: 60px;

  header {
    align-items: baseline;
    display: flex;
    flex-wrap: wrap;
    justify-content: space-between;
    border-bottom: 1px solid $clear;
  }

  .main-content {
    flex: 2;
    max-width: 780px;
    min-width: 525px;
  }

  .side-content {
    flex: 1;
    padding-top: beat(8);

    // max-width: 480px;
    min-width: 300px;

    h2 {
      @extend %heading-lg-medium;
    }

    p {
      @extend %body-sm-weak;

      @extend .text-medium;
    }

    figure {
      padding: beat(2) 0;
    }
  }
}



.dash-item {
  & + .dash-item {
    margin-top: beat(8);
    margin-bottom: 0;
  }

  dt {
    display: flex;
    justify-content: space-between;

    span {
      font-weight: 400;
    }
  }

  dd {
    color: #717171;
    margin-top: beat(2);
  }
}

.navbar-nav {
  button {
    height: 2rem;
    width: 2rem;
  }
}

.btn-toggler {
  display: none;
}

@include responsive(sm-up) {
  .dash-grid {
    display: grid;
    grid-template-columns: auto 1fr;
    grid-template-rows: auto;
    grid-template-areas: "choice stuff";
    flex: 1;

    #dashboard-navigation.collapse {
      display: block !important;
    }

    .controls {
      grid-area: choice;
      background-color: $white;
      height: 100%;

      nav {
        flex: auto;
      }
    }

    .content {
      grid-area: stuff;
    }
  }
}

@media 
  only screen 
  and (min-width: $screen-sm) 
  and (max-width: $screen-lg) 
  and (hover: none),
  only screen 
  and (min-width: $screen-sm) 
  and (max-width: $screen-lg) 
  and (any-pointer: course) {

  .touch-toggle {
    background-color: $white;
    display: block;

    button {
      background: transparent;
      border: none;
      width: 100%;

      &::before {
        content: url("/images/ui/icon-hamburger.svg");
        transform: translateY(5px) translateX(5px);
      }
    }
  }

}

@media only screen and (min-width: $screen-sm) and (max-width: $screen-lg) {

  .controls * > span {
    clip-path: $clip-close;
    transition: clip-path .5s ease, width 1s linear 10s,  max-width .5s ease, width 1s linear 10s;
    overflow-x: hidden;
    width: auto;
    padding: 0;
    max-width: 0px;
  }

  .controls:hover * > span {
    clip-path: $clip-open;
  }

  .inner-content {
    & > * {
      min-width: 100% !important;
    }
  }


}

@include responsive(lg) {
  .controls {
    max-width: 20rem;
    transition: max-width .5s ease;
  }

  .controls a > span {
    clip-path: $clip-open;
    transition: clip-path .5s ease, width 1s linear 10s,  max-width .5s ease, width 1s linear 10s;
    overflow-x: hidden;
    width: auto;
    padding: 0;
    max-width: 300px;
  }


}

@include responsive(xs) {
  .btn-toggler {
    align-items: center;
    display: flex;
    justify-content: center;
    padding: 0;

    &::before {
      content: url("/images/ui/icon-hamburger.svg");
      transform: translateY(3px);
    }

    &[aria-expanded="true"] {
      content: url("/images/ui/icon-hamburger-close.svg");
      padding: .5em;
    }
  }

  .inner-content {
    padding: 60px $grid-gutter-width;

    & > * {
      min-width: 100% !important;
    }
  }
}


.btn-notify {
  padding: 0;
  position: relative;

  &::before {
    content: url("/images/ui/dashboard/icon-bell.svg");
    display: block;
    transform: scale(1.3) translateY(4px);
  }

  &[data-newmsg="true"]::after {
    background-color: red;
    border-radius: 50%;
    content: '';
    display: block;
    height: .65rem;
    width: .65rem;
    position: absolute;
    top: 4px;
    right: 2px;
    z-index: 10;
  }
}