%light-space {
  background-color: $fill-white;
  // color: $text-medium;
}

%dark-space {
  --bs-bg-opacity: 1;
  background-color: rgba(var(--bs-dark-rgb), var(--bs-bg-opacity));
  color: $text-white;
}

%neutral-space {
  background-color: $fill-neutral-light;
  color: $text-strong;
}

%primary-space {
  background-color: $fill-primary;
  color: $text-white;
}


%shadow-small {
  box-shadow: map-get($shadow, 'small');
}

%shadow-medium {
  box-shadow: map-get($shadow, 'medium');
}

%shadow-large {
  box-shadow: map-get($shadow, 'large');
}

%btn-outline {
  background-color: var(--btn-outline-val-bg) !important;
  border-color: var(--btn-outline-val);
  color: var(--btn-outline-val);

  &:hover {
    background-color: var(--btn-outline-val-bg-hover) !important;
    border-color: var(--btn-outline-val-hover);
    color: var(--btn-outline-val-hover);
  }
}

%btn-solid {
  background-color: var(--btn-solid-val-bg) !important;
  border-color: var(--btn-solid-val);
  color: var(--btn-solid-val);

  &:hover {
    background-color: var(--btn-solid-val-bg-hover) !important;
    border-color: var(--btn-solid-val-hover);
    color: var(--btn-solid-val-hover);
  }
}



@each $size-name, $scale-step in $desktop-type-map {
  %body-#{$size-name} {
    font-size: var(--body-#{$size-name});
  }
  %body-#{$size-name}-weak {
    font-size: var(--body-#{$size-name});
    font-weight: 400;
  }
  %body-#{$size-name}-medium {
    font-size: var(--body-#{$size-name});
    font-weight: 500;
  }

  %heading-#{$size-name} {
    font-size: var(--heading-#{$size-name});
  }
  %heading-#{$size-name}-weak {
    font-size: var(--heading-#{$size-name});
    font-weight: 400;
  }
  %heading-#{$size-name}-medium {
    font-size: var(--heading-#{$size-name});
    font-weight: 500;
  }
  %heading-#{$size-name}-strong {
    font-size: var(--heading-#{$size-name});
    font-weight: 700;
  }
}


.light {
  @extend %light-space;
}

.inverse {
  @extend %dark-space;
}

.neutral {
  @extend %neutral-space;
}

.primary-bg {
  @extend %primary-space;
}

.shadow-small {
  background-color: pink;
  @extend %shadow-small;
}
.shadow-medium {
  @extend %shadow-medium;
}
.shadow-large {
  @extend %shadow-large;
}

