body {
  background-color: $fill-neutral;
}

.bg-neutral {
  background-color: $fill-neutral;
  
  &.light {
    background-color: $fill-neutral-light;
  }
  &.dark {
    background-color: $fill-neutral-dark;
  }
}

.text-strong {
  color: $text-strong;       
}       
.text-medium {
  color: $text-medium;       
}       
.text-weak {
  color: $text-weak;         
}         
.text-white {
  color: $text-white;        
}        
.text-neutral {
  color: $text-neutral;      
}      
.text-neutral-light {
  color: $text-neutral-light;
}
.text-neutral-dark {
  color: $text-neutral-dark; 
} 